










































import uniTag from '@/components/uni-tag.vue'
var limit = 10,
	page = 1;
var data = {
	/* "cmsNews.moduleId": moduleId, */
	"page": page,
	"limit": limit
}
export default {
	components: {
		uniTag
	},
	data() {
		return {
			listData: [],
			reload: false
		}
	},
	onLoad() {
		this.getList();
	},
	onPullDownRefresh() {
		this.reload = true;
		this.getList();
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getList() {
			data.page = page++;
			var url = "http://111.19.140.83/coreLawPerson/list.do";
			uni.request({
				url: url,
				data: data,
				success: (data) => {
					var rsp = data.data;
					if (rsp.code == 200) {
						let list = this.setTime(rsp.data);
						this.listData = this.reload ? list : this.listData.concat(list);
						this.reload = false;
					}
				},
				fail: (rsp, code) => {
					console.log('fail' + JSON.stringify(rsp));
				}
			})
		},

		goDetail: function(e) {
			let detail = {
				moduleName: e.moduleName,
				cover: e.cover,
				id: e.id,
				createTime: e.createTime,
				title: e.title
			}
			uni.navigateTo({
				url: "new_detail/new_detail?detail=" + encodeURIComponent(JSON.stringify(detail))
			})
		},
		setTime: function(items) {
			var newItems = [];
			items.forEach((e) => {
				var practiceMajors = e.practiceMajor.split(",");
				console.log(practiceMajors)
				newItems.push({
					name: e.name,
					icon: e.icon,
					id: e.id,
					organizationName: e.organizationName,
					consultNumber: e.consultNumber,
					phone: e.phone,
					degree: e.degree,
					typeName: e.typeName,
					practiceMajor:practiceMajors // 1,2,3
				});
			});
			return newItems;
		},
	},
}



